import React, { Component } from 'react'
import { NavBar, Icon,Tabs } from 'antd-mobile';
import { Carousel, WingBlank } from 'antd-mobile';
import { SearchBar, Button, WhiteSpace } from 'antd-mobile';
import {Link,Route} from 'react-router-dom'; 
export default class AppHome extends Component {
   
    render() {
        return (
            <div>
                <NavBar
                    style={{backgroundColor:'#3fcccb',color:'#ffffff'}}
                 
                    
                    rightContent={[
                        <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
                    ]}
                >商城</NavBar>
                <div className="cl">
                        <img 
                            src='image/12.png'
                            style={{width:'5%',height:'100%',float:'left',paddingLeft:'10'}}
                            />
                        <SearchBar style={{height:'20',backgroundColor:'black'}} placeholder="输入关键字进行搜索" maxLength={8} />
                        
                </div>
                        <Carousel
                            autoplay={true}
                            infinite
                            beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                            afterChange={index => console.log('slide to', index)}
                            >
                            {[1,2,3,4].map(val => (
                                <a
                                key={val}
                                // href="http://www.alipay.com"
                                style={{ display: 'inline-block', width: '100%', height: 200 }}
                                >
                             
                                {/* {val}
                                {val} */}
                                <img
                                    src="./image/1.png"
                                    alt=""
                                    style={{ width: '100%', verticalAlign: 'top' }}
                                    onLoad={() => {
                                    // fire window resize event to change height
                                    window.dispatchEvent(new Event('resize'));
                                    this.setState({ imgHeight: 'auto' });
                                    }}
                                />
                                </a>
                            ))}
                            </Carousel>  
                        <div>
                            <img 
                                src='image/桌子.png'
                                width='14%'
                                style={{paddingLeft:'8%',float:'left'}}
                                />
                            <img 
                                src='image/床.png'
                                width='18%'
                                style={{paddingLeft:'12%',float:'left',paddingTop:'1%'}}
                                />
                            <img 
                                src='image/椅子.png'
                                width='21%'
                                style={{paddingLeft:'13%',float:'left'}}
                                />
                            <img 
                                src='image/茶几.png'
                                width='18%'
                                style={{paddingLeft:'10%',float:'left'}}
                                />
                            <img 
                                src='image/柜子.png'
                                width='18%'
                                style={{paddingLeft:'10%',paddingTop:'2%'}}
                                />
                            <div className="cl1">
                                    <span className="cl2">桌</span>
                                    <span className="cl3">床</span>
                                    <span className="cl4">椅</span>
                                    <span className="cl5">几</span>
                                    <span className="cl6">柜</span>
                            </div> 
                            <img 
                                src='image/书架.png'
                                width='14%'
                                style={{paddingLeft:'8%',float:'left'}}
                                />
                            <img 
                                src='image/沙发.png'
                                width='18%'
                                style={{paddingLeft:'12%',float:'left',paddingTop:'1%'}}
                                />
                            <img 
                                src='image/家居饰品.png'
                                width='19%'
                                style={{paddingLeft:'13%',float:'left'}}
                                />
                            <img 
                                src='image/户外家具.png'
                                width='18%'
                                style={{paddingLeft:'13%',float:'left'}}
                                />
                            <img 
                                src='image/全部分类.png'
                                width='17%'
                                style={{paddingLeft:'9%'}}
                                />
                            <div className="cl1">
                                    <span className="cl2">书架</span>
                                    <span className="cl3">沙发</span>
                                    <span className="cl4">家居</span>
                                    <span className="cl5">户外</span>
                                    <span className="cl6">全部</span>
                            </div> 
                         
                                <img 
                                    src='image/13.png'
                                    width='48%'height='100%'
                                    style={{paddingLeft:'12%',float:'left'}}
                                    />
                 
                           
                                <img 
                                    src='image/14.png'
                                    width='39%'height='100%'
                                    style={{paddingLeft:'3%'}}
                                    />
                      
                        </div>                           
            </div>
        )
    }
}